body {
    background-image: url('../images/rainbow.tiny.jpeg');
    animation: moveIt 10s linear infinite;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    width: 100%;
    font-family: 'Oswald', sans-serif;

}
.blockOne {
    width: 100%;
    height: 350px;
    background-color: white;
    margin-left: auto;
    margin-right: auto;
    position: fixed;
    left: 0;
    top: 50px;
}
.logo {
    height: 45px;
    width: 45px;
    position: relative;
    padding-top: 5px;
    padding-bottom: 1px;
    padding-left: 5px;
}
.navbar {
    background-color: rgb(88, 90, 224);
    position: fixed;
    top: 0;
    left:0;
    right:0;
    bottom: 0;
    z-index: 0;
    opacity: .9;
    position: fixed;
    width: 100%;
    height: 50px;
    top: 0;
    left: 0;
}
.welcomeBlock {
    position: absolute;
    top: 0;
    left:0;
    right:0;
    bottom: 0;
    z-index: 1;
    padding-top: 40px;
    padding-left: auto;
    padding-right: auto;
    top: 100px;
    border: white solid 10px;
    background: rgba(49, 48, 48, 0.863);
    margin-left: 30px;
    margin-right: 30px;

}
.welcome {
    position: relative;
    text-align: center;
    font-family: 'Oswald', sans-serif;
    font-size: 200px;
    color: white;
    padding-left: 10px;
    padding-right: 10px;
    animation: fadeInAnimation ease 4s; 
    animation-iteration-count: 1; 
    animation-fill-mode: forwards;
    margin-bottom: 190px;
}
.welcomeDate {
    position: fixed;
    bottom: 3%;
    color: white;
    border: white solid 3px;
    padding: 5px;
    background: rgba(49, 48, 48, 0.863);
    margin-left: 30px;
    font-size: 20px;

} 
.monochromeText {
    font-size: 35px;
    padding-left: 8px;
    padding-bottom: 5px;
    color: white;
    position: relative;
    animation: fadeInAnimation ease 9s; 
    animation-iteration-count: 1; 
    animation-fill-mode: forwards;
    
}
.rainbowText {
    animation-name: rainbow 6s linear 2s infinte;
    background: linear-gradient(to right, #6666ff, #0099ff , #00ff00, #ff3399, #6666ff);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    animation: rainbow 6s ease-in-out infinite;
    background-size: 400% 100%;
}
.arrowDown {
    position: relative;
    left: 50%;
    right: 50%;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
	transform: scale(1);
	animation: pulse 2s infinite;

}
.blockOne {
    position: absolute;
    background-color: white;
    top: 850px;
    left:0;
    right:0;
    bottom: 0;
    z-index: 2;
    height: 350px;
    padding-left: 30px;
    padding-right: 30px;




}
.blockTwo {
    position: absolute;
    top: 1150px;
    left:0;
    right:0;
    bottom: 0;
    z-index: 3;
    height: 200px;
}
.myName {
    font-family: 'Oswald', sans-serif;
    font-size: 85px;
    text-align: center;
    color: rgb(88, 90, 224);
    padding-top: auto;

}
.introText {
    font-size: 18px;


    
}
.profilePhoto {
    height: 150px;
    width: 150px;
    /* margin-left: 30px; */
    margin-right: 30px;
    margin-bottom: 10px;
    float: left;

}



@keyframes pulse {
	0% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
	}

	70% {
		transform: scale(1);
		box-shadow: 0 0 0 10px rgba(0, 0, 0, 0);
	}

	100% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
	}
}
@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes rainbow {
    from {
        color: #2828f0;
      }
      10% {
        color: #0099ff;
      }
      50% {
        color: #00ff00;
      }
      75% {
        color: #ff002b;
      }
      100% {
        color: #fac124;
      }
      0%,100% {
        background-position: 0 0;
    }
        50% {
        background-position: 100% 0;
    }
}
@keyframes moveIt {
    from {background-position: bottom left;}
    to {background-position: top right;}
  }
/* Media Query Mobile Devices */
@media screen and (max-width: 480px) {

    .welcome {
        font-size: 25px;
    }
    .welcomeBlock {
        position: relative;
        padding-top: 40px;
        padding-left: auto;
        padding-right: auto;
        top: 70px;
    
    }

  }

/* Media Query Tablets */
@media screen and (max-width: 768px) {
    .welcome {
        font-size: 115px;
        position: relative;
    }
    .welcomeBlock {
        position: relative;
        padding-top: 40px;
        padding-left: auto;
        padding-right: auto;
    
    }
}

/*Media Query Small Laptops */
@media screen and (max-width: 1024px) {
    .welcomeBlock {
        position: relative;
        padding-top: 40px;
        padding-left: auto;
        padding-right: auto;
        top: 100px;
    }
}